@import url(https://fonts.googleapis.com/css?family=Major+Mono+Display);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f2f5fa;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.homeContent{
    height: 100vh;
    background: url(/static/media/background-home.72610b38.png) center/cover;
    width: 100%;
    position: absolute;
    z-index: 99;
}

.textLeft{
    position: absolute;
    left: calc(5% - 10px);
    top: 300px;
}

.textLeft h1{
    color: #ac3bf2;
    font-size: 34pt;
    width: 50%;
    font-family: 'Major Mono Display', sans-serif;
    font-weight: 700;
}

.textLeft p{
    color: #9e81d1;
    width: 30%;
    font-size: 14pt;
    font-weight: 300;
    text-align: justify;
}

@media only screen and (max-width: 1440px) {
    .homeContent{
        background: url(/static/media/background-home-1440.fbb7f547.png) center/cover;
    }
}

@media only screen and (max-width: 1024px) {
    .homeContent{
        background: url(/static/media/background-home-1024.6648a935.png) center/cover;
    }
    .textLeft h1 {
        color: #ac3bf2;
        font-size: 26pt;
        width: 32%;
        font-family: 'Major Mono Display', sans-serif;
        font-weight: 700;
    }
}

@media only screen and (max-width: 600px) {
    .homeContent{
        background: url(/static/media/background-home-600.e2e25d9e.png) center/cover;
    }
    .textLeft{
        top: 350px;
        left: 30px;
    }
    .textLeft p{
        color: #9e81d1;
        width: 80%;
        font-size: 12pt;
        font-weight: 300;
        text-align: justify;
    }
    .textLeft h1 {
        color: #ac3bf2;
        font-size: 20pt;
        width: 100%;
        font-family: 'Major Mono Display', sans-serif;
        font-weight: 700;
    }
}
.PeriodicTable{
    display: grid;
    grid-template-columns: auto auto auto auto;
    padding: 45px;
    grid-column-gap: 20px;
    background: #f2f5fa;
    z-index: -99;
    padding-top: 100px;
}

.search{
    padding-top: 100px;
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100%;
}

@media only screen and (max-width: 1024px) {
    .PeriodicTable{
        grid-template-columns: auto auto auto;
    }
}

@media only screen and (max-width: 768px) {
    .PeriodicTable{
        grid-template-columns: auto auto;
    }
}

@media only screen and (max-width: 600px) {
    .PeriodicTable{
        grid-template-columns: auto;
        padding: 10px;
    }
    .search{
        padding-top: 70px
    }
}
.backgroundlogo{
    -webkit-filter: blur(8px) grayscale(50%) saturate(0);
            filter: blur(8px) grayscale(50%) saturate(0);
    height: 400px;
    padding: 45px;
    width: 106%;
    margin-left: -20px;
}
.shellAvatar {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.avatarElemento {
    width: 200px;
    height: 200px;
    border-radius: 100px;
    box-shadow: 0px 2px 8px #00000054;
    margin-bottom: 20px;
    position: absolute;
    z-index: 3;
    top: 150px
}

.nomeElemento{
    position: absolute;
    top: 360px;
    color: white;
    z-index: 3;
    font-weight: 300;
    text-shadow: 0 2px 4px #00000080;
}

.contentSobreElemento{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 24px;
    position: relative;
    background: #f2f5fa !important;
    margin-top: -10px;
}

.perfilElementoWrapper {
    background: #fff;
    width: 135px;
    height: 135px;
    border-radius: 20px;
    text-align: center;
    box-shadow: 0px 2px 8px #0000002b;
}

.listInformacoes{
    display: grid;
    grid-template-columns: auto auto auto auto;
    padding: 45px;
    grid-column-gap: 20px;
}

@media only screen and (max-width: 1440px) {
    .listInformacoes{
        grid-template-columns: auto auto auto;
    }
}

@media only screen and (max-width: 1024px) {
    .listInformacoes{
        grid-template-columns: auto auto;
    }
}

@media only screen and (max-width: 768px) {
    .listInformacoes{
        grid-template-columns: auto;
        padding: 10px;
    }
}
